
.app {
  width: 200px;
  height: 200px;
  background-color: #ccc;
  margin-right: 100px;
  display: inline-block;
  position: relative;
}
.right-menu {
  padding: 10px 0;
  list-style: none;
  background-color: #f6f6f6;
  font-size: 13px;
  z-index: 8000;
}
.menu-item-disabled {
  color: #c0c4cc;
}
li {
  cursor: pointer;
  padding: 5px 20px;
  color: #5d6d7e;
}
li:not(.menu-item-disabled):hover {
  background-color: #32c081;
  color: white;
}



